.App {
  font-family: 'Arial', sans-serif;
  max-width: 100%;
  background-color: rgb(245, 232, 216);
}

h1 {
  color: rgb(177, 59, 16); 
  font-size: 32px; 
}

p {
  color: rgb(81, 77, 77); 
}

img {
  width: 50%;
  height: auto;
  border-radius: 6px; 
}

ul, ol {
  font-size: 18px; 
  margin-left: 40px;
  padding-left: 20px;
  color: rgb(81, 77, 77);
}

ul li, ol li {
  margin-bottom: 15px;
}

body {
  background: rgb(43, 43, 37);
}

@media screen and (max-width: 768px) {
  .App {
    padding: 15px;
  }

  h1 {
    font-size: 28px;
  }

  p, ul, ol {
    font-size: 16px;
  }

  img {
    width: 80%; 
  }
}
